import {GetAskView,ReplyAskTopic,CloseAsk,SetAskSatisfied,AddReward,DeleteAnswer} from '@/api/class-center'
import KsImage from '@/components/ks-components/image/Image.vue'
import KsCollect from '@/components/ks-components/collect/collect.vue'
import KsEmpty from '@/components/ks-components/empty/empty.vue'
import UniLoadMore from '@/components/uni/uni-load-more/uni-load-more.vue'
export default {
	data(){
		return{
			baseUrl:this.$store.state.SRC+"/config/app",
			loaded:false,
			askData:{},
			getParameters:{
				topicId:0,
				pageindex:1,
				pagesize:10,
			},
			rewardArry:['无悬赏分','1分','5分','10分','15分','20分','30分','50分','80分','100分'],
			isCollect:false,
			conIsShow:false,
			pageCount:0,
			moreStatus:'more',
			isReply:false,
			isFocus:false,
			content:'',
			isMyself:false,
			collectClass:'',
			overHeight:false,
			iscollect:false,
			isHaveBest:false,
			rewardIndex:0,
			bottomHeight:-243
		}
	},
	components:{
		KsImage,
		KsCollect,
		KsEmpty,
		UniLoadMore
	},
	onLoad: function(option) {
		this.getParameters.topicId = option.topicid
		uni.showLoading({
		    title: '加载中'
		});
		this.loadPage()
		
	},
	onReachBottom(e){
		this.getParameters.pageindex++
		this.moreList()
	},
	onShareAppMessage(){
		var that = this
	　　// 设置菜单中的转发按钮触发转发事件时的转发内容
	　　var shareObj = {
	　　　　title: that.askData.title,
			path: '/pages/class-center/ask/details?topicid='+that.getParameters.topicId,
	　　　　imageUrl: that.askData.imgList[0]||this.baseUrl+'/static/images/common/default-img.png', 
	　　　　success: function(res){
	　　　　　　// 转发成功之后的回调
	　　　　　　if(res.errMsg == 'shareAppMessage:ok'){
	　　　　　　}
	　　　　},
	　　　　fail: function(){
	　　　　　　// 转发失败之后的回调
	　　　　　　if(res.errMsg == 'shareAppMessage:fail cancel'){
	　　　　　　　　// 用户取消转发
	　　　　　　}else if(res.errMsg == 'shareAppMessage:fail'){
	　　　　　　　　// 转发失败，其中 detail message 为详细失败信息
	　　　　　　}
	　　　　}
	　　};
	　　// 返回shareObj
	　　return shareObj;
	},
	methods: {
		bindPickerChange(e){
			this.rewardIndex = e.target.value
			let scoreValue = this.rewardArry[e.target.value]
			let score
			if(scoreValue=='无悬赏分'){
				score = 0
			}else{
				score = Number(scoreValue.split('分')[0])
			}
			uni.showLoading({
			    title: '正在提交中'
			});
			AddReward({
				topicid:this.getParameters.topicId,
				score:score
			}).then(res=>{
				uni.hideLoading();
				if(res.result){
					uni.showToast({
					    title: '设置成功',
					    duration: 2000,
						icon:'none',
					});
					this.loadPage()
				}else{
					uni.showToast({
					    title: res.msg,
					    duration: 2000,
						icon:'none',
					});
				}
			})
		},
		setBottomHeight(e){
			this.isFocus = true
			if(this.isReply){
				this.bottomHeight = e.detail.height 
			}
		},
		setNoHeight(){
			this.isFocus = false
			if(this.isReply){
				this.bottomHeight = 0
			}else{
				this.bottomHeight = -243
			}
			
		},
		closeReply(){
			this.isReply=false
			this.isFocus=false
			this.bottomHeight = -243
			this.content = ''
		},
		deleteReply(topicid,postid){
			uni.showLoading({
			    title: '正在提交中'
			});
			DeleteAnswer({
				topicid:topicid,
				postid:postid
			}).then(res=>{
				uni.hideLoading();
				if(res.result){
					uni.showToast({
					    title: '删除成功',
					    duration: 2000,
						icon:'none',
					});
					this.loadPage()
				}else{
					uni.showToast({
					    title: res.msg,
					    duration: 2000,
						icon:'none',
					});
				}
			})
		},
		setBestAsk(topicid,postid){
			uni.showLoading({
			    title: '正在提交中'
			});
			SetAskSatisfied({
				topicid:topicid,
				postid:postid
			}).then(res=>{
				uni.hideLoading();
				if(res.result){
					uni.showToast({
					    title: '设置成功',
					    duration: 2000,
						icon:'none',
					});
					this.loadPage()
				}else{
					uni.showToast({
					    title: res.msg,
					    duration: 2000,
						icon:'none',
					});
				}
			})
		},
		setNoSatisfaction(){
			let _this = this
			uni.showModal({
			    title: '无满意答案',
			    content: '如果没有满意的回答，可点击“关闭问题”按钮结束提问',
			    success: function (res) {
			        if (res.confirm) {
			            _this.closeAsk()
			        } else if (res.cancel) {
			            console.log('用户点击取消');
			        }
			    }
			});
		},
		closeAsk(){
			uni.showLoading({
			    title: '正在提交中'
			});
			CloseAsk({
				topicid:this.getParameters.topicId
			}).then(res=>{
				uni.hideLoading();
				if(res.result){
					uni.showToast({
					    title: '关闭成功',
					    duration: 2000,
						icon:'none',
					});
					this.loadPage()
				}else{
					uni.showToast({
					    title: res.msg,
					    duration: 2000,
						icon:'none',
					});
				}
			})
		},
		moreList(){
			if(this.getParameters.pageindex > this.pageCount){
				this.moreStatus = 'noMore'
			}else{
				this.moreStatus = 'loading'
				this.loadPage('more')
			}
		},
		loadPage(status){
			if(status=='more'){
				GetAskView(this.getParameters).then(res=>{
					this.moreStatus = 'more'
					if(res.result){
						res.replyList.map((item,i)=>{
							item.isShow = false
						})
						this.askData.replyList = this.askData.replyList.concat(res.replyList)
					}
					this.loaded = true
				})
			}else{
				this.getParameters.pageindex = 1
				GetAskView(this.getParameters).then(res=>{
					if(res.result){
						this.pageCount = res.pageCount
						if(this.pageCount==1){
							this.moreStatus = 'noMore'
						}else{
							this.moreStatus = 'more'
						}
						this.isMyself = res.ismine
						this.iscollect = res.iscollect
						if(res.bestusername){
							this.isHaveBest = true
						}else{
							this.isHaveBest = false
						}
						res.replyList.map((item,i)=>{
							item.overHeight = false
							item.isShow = false
						})
						this.askData = res
						if(this.askData.issolve=='0'){
							if(this.isMyself){
								this.collectClass = 'ks-col-5'
							}else{
								this.collectClass = 'ks-col-6'
							}
						}else{
							this.collectClass = 'ks-col-6'
							
						}
					}else{
						this.pageCount = 0
					}
					this.loaded = true
					uni.hideLoading();
					this.$nextTick(function(){
						const query = uni.createSelectorQuery().in(this);
						query.select('#ask-con-show').boundingClientRect(data => {  
						  if(data.height>40){
							  this.overHeight = true
						  }else{
							  this.overHeight = false
						  }
						}).exec();
						this.askData.replyList.map((item,i)=>{
							const query = uni.createSelectorQuery().in(this);
							query.select('#reply'+i).boundingClientRect(data => {  
							  if(data.height>40){
								  item.overHeight = true
							  }else{
								  item.overHeight = false
							  }
							}).exec();
						})
					})
				})
				
			}
		},
		setShow(action,index){
			if(action=='reply'){
				this.askData.replyList[index].isShow = this.askData.replyList[index].isShow==false?true:false
			}else{
				this.conIsShow = this.conIsShow==false?true:false
			}
		},
		replyQuestion(){
			this.isReply = true
			this.bottomHeight = 0
		},
		cancel(){
			this.isReply=false
			this.isFocus = false
			this.bottomHeight = -243
			this.content = ''
		},
		submit(){
			if(this.content){
				uni.showLoading({
				    title: '正在提交中'
				});
				ReplyAskTopic({
					topicid:this.getParameters.topicId,
					content:this.content
				}).then(res=>{
					uni.hideLoading();
					if(res.result){
						uni.showToast({
						    title: '提交回答成功',
						    duration: 2000,
							icon:'none',
						});
						this.loadPage()
					}else{
						uni.showToast({
						    title: res.msg,
						    duration: 2000,
							icon:'none'
						});
					}
					this.content = ''
					this.isReply = false
					this.isFocus = false
					this.bottomHeight = -243
				})
			}else{
				uni.showToast({
				    title: '请输入回答内容',
				    duration: 10000,
					icon:'none'
				});
			}
		}
	}
}
